.gridContainer {
  display: grid;
  /*       5rem */
  grid-template-columns: repeat(auto-fill, minmax(5rem, 1fr));
  gap: 1rem; /*    */
}

/*     */
.tile {
  border: 1px solid #000; /*   */
  height: 8rem; /*   */
  border-radius: 0.8rem; /*   */
  background-color: #e0e0e0; /*   */
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  color: #333;
}

.tile span {
  font-size: 4rem;
  user-select: none;
}

.tileGhost {
  opacity: 0;
}

.screen {
  padding-block: 1rem;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none; /* IE and Edge */
  height: 100%;
  padding-inline: 1rem;
}

.screen::-webkit-scrollbar {
  display: none;
}
